import React, { useState, useEffect, useCallback } from 'react';
import {
  Box,
  Paper,
  Typography,
  Grid,
  Card,
  CardContent,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  CircularProgress,
} from '@mui/material';
import axios from 'axios';

const AccountingReport = () => {
  const [timeRange, setTimeRange] = useState('DAILY');
  const [report, setReport] = useState(null);
  const [loading, setLoading] = useState(true);

  const fetchReport = useCallback(async () => {
    try {
      setLoading(true);
      const response = await axios.get(`http://localhost:8080/api/accounting/report?range=${timeRange}`);
      setReport(response.data);
    } catch (error) {
      console.error('   :', error);
    } finally {
      setLoading(false);
    }
  }, [timeRange]);

  useEffect(() => {
    fetchReport();
  }, [fetchReport]);

  const handleRangeChange = (event) => {
    setTimeRange(event.target.value);
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom>
         
      </Typography>

      <FormControl sx={{ mb: 3, minWidth: 200 }}>
        <InputLabel> </InputLabel>
        <Select
          value={timeRange}
          onChange={handleRangeChange}
          label=" "
        >
          <MenuItem value="DAILY"></MenuItem>
          <MenuItem value="WEEKLY"></MenuItem>
          <MenuItem value="MONTHLY"></MenuItem>
        </Select>
      </FormControl>

      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                 
              </Typography>
              <Typography variant="h4">
                {report?.totalSales.toLocaleString()}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                 
              </Typography>
              <Typography variant="h4">
                {report?.totalCost.toLocaleString()}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                 
              </Typography>
              <Typography variant="h4" color="primary">
                {report?.totalProfit.toLocaleString()}
                ({Math.round(report?.totalProfit / report?.totalSales * 100)}%)
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Paper sx={{ mt: 3 }}>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell align="right"></TableCell>
                <TableCell align="right"></TableCell>
                <TableCell align="right"></TableCell>
                <TableCell align="right"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {report?.dailyReports.map((daily) => (
                <TableRow key={daily.date}>
                  <TableCell>{daily.date}</TableCell>
                  <TableCell align="right">{daily.sales.toLocaleString()}</TableCell>
                  <TableCell align="right">{daily.cost.toLocaleString()}</TableCell>
                  <TableCell align="right">{daily.profit.toLocaleString()}</TableCell>
                  <TableCell align="right">{Math.round(daily.profit / daily.sales * 100)}%</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </Box>
  );
};

export default AccountingReport; 